import { useLoginClient } from "@/context/LoginClientContext"; // تأكد من المسار الصحيح

function ProfileModal({ onClose }: { onClose: () => void }) {
  const [name, setName] = React.useState("");
  const [companyName, setCompanyName] = React.useState("");
  const { saveData, updateAccountInfo } = useLoginClient();

  React.useEffect(() => {
    if (saveData) {
      setName(saveData.displayName || "");
      setCompanyName(saveData.companyName || "");
    }
  }, [saveData]);

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    try {
      await updateAccountInfo({
        displayName: name,
        companyName: companyName,
      });
      onClose();
    } catch (err) {
      alert("حدث خطأ أثناء تحديث البيانات");
    }
  }

  return (
    <div
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        background: "rgba(0,0,0,0.15)",
        zIndex: 1000,
        display: "flex",
        justifyContent: "flex-end",
      }}
    >
      <div
        style={{
          background: "#fff",
          width: 400,
          maxWidth: "100%",
          height: "100%",
          borderRadius: "16px 0 0 16px",
          boxShadow: "0 4px 24px rgba(0,0,0,0.10)",
          padding: 32,
          position: "relative",
          display: "flex",
          flexDirection: "column",
          gap: 16,
        }}
      >
        {/* Close button */}
        <button
          onClick={onClose}
          style={{
            position: "absolute",
            top: 16,
            right: 16,
            background: "none",
            border: "none",
            fontSize: 22,
            cursor: "pointer",
            color: "#888",
          }}
          aria-label="Close"
        >
          &#10005;
        </button>

        <h3 style={{ marginBottom: 16, fontWeight: 600, fontSize: 20 }}>
          Edit personal information
        </h3>

        <form
          onSubmit={handleSubmit}
          style={{ display: "flex", flexDirection: "column", gap: 12 }}
        >
          <div>
            <label style={{ fontSize: 13, color: "#444" }}>Full name</label>
            <input
              value={name}
              onChange={(e) => setName(e.target.value)}
              style={inputStyle}
              required
            />
          </div>

          <div>
            <label style={{ fontSize: 13, color: "#444" }}>Company name</label>
            <input
              placeholder="Enter your Company name"
              value={companyName}
              onChange={(e) => setCompanyName(e.target.value)}
              style={inputStyle}
            />
            <div style={{ fontSize: 12, color: "#888", marginTop: 2 }}>
              This is your public profile name.
            </div>
          </div>

          <div
            style={{
              display: "flex",
              justifyContent: "flex-end",
              gap: 8,
              marginTop: 16,
            }}
          >
            <button
              type="button"
              onClick={onClose}
              style={{
                padding: "8px 24px",
                borderRadius: 6,
                border: "1px solid #22c55e",
                background: "#fff",
                color: "#22c55e",
                fontWeight: 500,
                cursor: "pointer",
              }}
            >
              Cancel
            </button>
            <button
              type="submit"
              style={{
                padding: "8px 24px",
                borderRadius: 6,
                border: "none",
                background: "#22c55e",
                color: "#fff",
                fontWeight: 500,
                cursor: "pointer",
              }}
            >
              Save
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}
